.space {
  height: 100vh;
  display: flex;
  background-color: #00000050;
  .ant-menu,
  .row-menu {
    background: #ffffff60;
  }
  .column-menu {
    height: 100%;
  }
  .row-menu {
    height: 48px;
    border-bottom: 1px solid #fff;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 0 16px;
  }
  .main-right {
    flex: 1;
    // margin-left: 12px;
    .child-pages {
      height: calc(100% - 48px);
    }
  }
  .child-pages {
    // background: #000000;
    box-sizing: border-box;
  }
}
.ant-drawer {
  h4 {
    font-size: 14px;
    font-weight: 600;
    line-height: 1.5;
    margin: 12px 0;
  }
  .imgs {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    li {
      width: 100px;
      margin-bottom: 8px;
      opacity: 0.5;
      &.active{
        opacity: 1;
      }
    }
  }
}
