.Index {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  &::before {
    content: " ";
    display: block;
    width: 100%;
    height: 100%;
    background-image: attr(data-backgroundImage);
    background-position: center center;
    background-repeat: no-repeat;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
  }

  header {
    width: 100vw;
    height: 40px;
    position: fixed;
    top: 0;
    left: 0;
    background: #00000010;
    ul {
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: flex-end;
      li {
        cursor: pointer;
        color: #ffffff70;
        font-size: 16px;
        padding: 0 16px;
      }
    }
  }

  .list {
    width: 648px;
    display: flex;
    flex-wrap: wrap;
    li {
      cursor: pointer;
      width: 200px;
      height: 200px;
      border-radius: 24px;
      background-color: #00000f30;
      margin: 0 24px 24px 0;
      box-shadow: 0px 0px 20px #ffffff70 inset;
      a {
        color: #ffffff90;
        font-size: 26px;
        font-family: serif, Georgia, "Times New Roman", Times;
        width: 100%;
        height: 100%;
        user-select: none;
        display: flex;
        align-items: center;
        justify-content: center;
      }
      &:nth-child(3n) {
        margin-right: 0;
      }
      &:hover,
      &:active,
      &:focus {
        color: #fff;
        background: #00000070;
        i {
          color: #fff;
        }
      }
    }
  }
}
.background-image-list {
  color: #fff;
  font-size: 26px;
  li {
    width: 100%;
    display: flex;
    align-items: center;
    margin-bottom: 24px;
    img {
      object-fit: contain;
    }
  }
}
