.Portfolio {
  height: 100vh;
  .container {
    height: 100vh;
  }
}
.ant-drawer {
  .block {
    .target {
      width: 100%;
      font-size: 24px;
      text-align: justify;
    }
    .color,
    .item {
      display: flex;
      align-items: center;
      padding: 4px 0;
    }
    span.tag {
      display: inline-block;
      width: 130px;
      text-align: center;
      font-size: 16px;
      // font-weight: bold;
      &::after{
          content: '：';
          float: right;
      }
    }
  }
  .group {
    font-size: 18px;
    font-weight: bold;
    line-height: 2;
  }
}
