.attachment{
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: serif;
    .box{
        width: 50vw;
        height: 50vh;
        overflow-y: scroll;
    }
    .backgroundImage{
        height: 100%;
        background-size: 50vw;
        background-repeat: no-repeat;
        background-attachment: fixed;
    }
}