.Effect {
  min-height: 50vh;
  overflow-y: scroll;
  background-image: url("~assets/images/1.png");
  background-repeat: no-repeat;
  background-size: cover;
  // fixed 背景图片宽度自动设置为100vw, 会随着元素被隐藏，不会随着元素滚动而滚动， 位置始终固定, 对于元素内滚动也处于fixed状态
  // scroll 不会随着元素内容滚动
  // local 会随着元素内容滚动
  background-attachment: fixed;
  .article {
    display: inline-block;
    .text {
      width: 100%;
      h3 {
        font-size: 26px;
        color: #ffffff90;
        text-align: left;
        line-height: 88px;
      }
    }
  }
}
.a {
  height: 100vh;
}
