.component-background {
  width: 100%;
  position: relative;
  .background {
    position: absolute;
    width: 100%;
    height: 100%;
    .image {
      position: absolute;
      width: inherit;
      height: inherit;
      top: 0;
      left: 0;
    }
  }
}
