.index_Index__Xibli{
  width:100vw;
  height:100vh;
  display:flex;
  justify-content:center;
  align-items:center;
  position:relative
}

.index_Index__Xibli::before{
  content:" ";
  display:block;
  width:100%;
  height:100%;
  background-image:attr(data-backgroundImage);
  background-position:center center;
  background-repeat:no-repeat;
  position:absolute;
  top:0;
  left:0;
  z-index:-1
}

.index_Index__Xibli header{
  width:100vw;
  height:40px;
  position:fixed;
  top:0;
  left:0;
  background:#00000010
}

.index_Index__Xibli header ul{
  width:100%;
  height:100%;
  display:flex;
  align-items:center;
  justify-content:flex-end
}

.index_Index__Xibli header ul li{
  cursor:pointer;
  color:#ffffff70;
  font-size:16px;
  padding:0 16px
}

.index_Index__Xibli .index_list__2Tj-T{
  width:648px;
  display:flex;
  flex-wrap:wrap
}

.index_Index__Xibli .index_list__2Tj-T li{
  cursor:pointer;
  width:200px;
  height:200px;
  border-radius:24px;
  background-color:#00000f30;
  margin:0 24px 24px 0;
  box-shadow:0px 0px 20px #ffffff70 inset
}

.index_Index__Xibli .index_list__2Tj-T li a{
  color:#ffffff90;
  font-size:26px;
  font-family:serif, Georgia, "Times New Roman", Times;
  width:100%;
  height:100%;
  -webkit-user-select:none;
  -ms-user-select:none;
  user-select:none;
  display:flex;
  align-items:center;
  justify-content:center
}

.index_Index__Xibli .index_list__2Tj-T li:nth-child(3n){
  margin-right:0
}

.index_Index__Xibli .index_list__2Tj-T li:hover,.index_Index__Xibli .index_list__2Tj-T li:active,.index_Index__Xibli .index_list__2Tj-T li:focus{
  color:#fff;
  background:#00000070
}

.index_Index__Xibli .index_list__2Tj-T li:hover i,.index_Index__Xibli .index_list__2Tj-T li:active i,.index_Index__Xibli .index_list__2Tj-T li:focus i{
  color:#fff
}

.index_background-image-list__wZcqm{
  color:#fff;
  font-size:26px
}

.index_background-image-list__wZcqm li{
  width:100%;
  display:flex;
  align-items:center;
  margin-bottom:24px
}

.index_background-image-list__wZcqm li img{
  object-fit:contain
}

